import { Component, h, State } from '@stencil/core';

/**
 * Trigger interactions
 * The element that is used for triggering the popover can be
 * set to interacted with in
 */

@Component({
    tag: 'limel-example-popover-trigger-interaction',
    shadow: true,
})
export class PopoverTriggerInteractionExample {
    @State()
    private isOpen = false;

    public render() {
        return [
            <limel-popover
                triggerInteraction="hover"
                open={this.isOpen}
                onOpen={this.onPopoverOpen}
                onClose={this.onPopoverClose}
            >
                <limel-button slot="trigger" label="Hover me!" />
                <p style={{ margin: '0.5rem 1rem' }} tabindex="0">
                    Content
                </p>
            </limel-popover>,
        ];
    }

    private onPopoverOpen = () => {
        this.isOpen = true;
    };

    private onPopoverClose = (event: CustomEvent) => {
        event.stopPropagation();
        console.log('closing');
        this.isOpen = false;
    };
}
